import { Component } from '@angular/core';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { filter } from 'rxjs/operators';
import { environment } from '../environments/environment';
import {
  animate,
  group,
  query,
  style,
  transition,
  trigger
} from '@angular/animations';
import { PwaService } from './services/pwa.service';
import { ScrollService } from './services/scroll.service';
import AOS from 'aos';

declare var gtag;

export function transitionAnimation(
  translateEnter: string,
  translateLeave: string
) {
  return [
    style({ height: '!' }),
    query(':enter', style({ transform: `translateX(${translateEnter})` })),
    query(
      ':enter, :leave',
      style({ position: 'absolute', top: 65, left: 0, right: 0 })
    ),
    // animate the leave page away
    group([
      query(':leave', [
        animate(
          '0.3s cubic-bezier(.35,0,.25,1)',
          style({ transform: `translateX(${translateLeave})` })
        )
      ]),
      // and now reveal the enter
      query(
        ':enter',
        animate(
          '0.3s cubic-bezier(.35,0,.25,1)',
          style({ transform: 'translateX(0)' })
        )
      )
    ])
  ];
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    trigger('routeAnimation', [
      transition(`1 => 2`, transitionAnimation('100%', '-100%')),
      transition(`2 => 1`, transitionAnimation('-100%', '100%'))
    ])
  ]
})
export class AppComponent {
  title = 'hajaniain';
  constructor(
    private router: Router,
    private pwaService: PwaService,
    private scrollService: ScrollService
  ) {
    AOS.init({
      duration: 1200
    });
    this.initGoogleAnalytics(router);
    this.pwaService.check();
    this.onRouterChange();
  }

  onRouterChange() {
    this.router.events.subscribe((routerEvent: RouterEvent) => {
      // scroll to hash ex: /#presentation
      setTimeout(() => {
        const hash = this.scrollService.getCurrentHash();
        if (hash) {
          this.scrollService.scroll();
        }
      }, 200);
    });
  }

  private initGoogleAnalytics(router: Router) {
    const script = document.createElement('script');
    script.async = true;
    script.src =
      'https://www.googletagmanager.com/gtag/js?id=' +
      environment.analyticsCode;
    const documentHead = document.head;
    (<any>documentHead).prepend(script);
    const navEndEvent$ = router.events.pipe(
      filter(e => e instanceof NavigationEnd)
    );
    navEndEvent$.subscribe((e: NavigationEnd) => {
      gtag('config', 'UA-134231114-1', { page_path: e.urlAfterRedirects });
    });
  }

  getDepth(outlet) {
    return outlet.activatedRouteData['depth'];
  }
}
